<template>
  <div class="page-container">
    <div class="page-section">
      <DxDataGrid
        id="data-grid-style"
        key-expr="id"
        :data-source="dataList"
        :element-attr="dataGridAttributes"
        :hover-state-enabled="true"
        :allow-column-reordering="true"
        :show-borders="true"
        :show-row-lines="true"
        :row-alternation-enabled="false"
        :column-hiding-enabled="true"
        :word-wrap-enabled="true"
        @row-inserted="CREATE_RECORD"
        @row-updated="UPDATE_RECORD"
        @row-removed="DELETE_RECORD"
        @editing-start="EDIT_START"
        @saved="SAVE"
      >
        <DxEditing
          :allow-updating="true"
          :allow-deleting="true"
          :allow-adding="true"
          :use-icons="true"
          mode="form"
        >
          <DxForm label-location="top">
            <DxItem :col-count="2" :col-span="2" item-type="group">
              <DxItem data-field="cert_no" :col-span="1" />
              <DxItem data-field="visual_by" :col-span="1" />
              <DxItem data-field="visual_test_date" :col-span="1" />
              <DxItem data-field="visual_test_result" :col-span="1" />
              <DxItem data-field="file_path" :col-span="2" />
            </DxItem>
          </DxForm>
        </DxEditing>

        <DxColumn data-field="cert_no" caption="Certificate No" :width="200">
          <DxRequiredRule />
        </DxColumn>
        <DxColumn data-field="visual_by" caption="Visual By" :width="150">
          <DxRequiredRule />
        </DxColumn>
        <DxColumn
          data-field="visual_test_date"
          caption="Inspection Date"
          data-type="date"
          format="dd MMM yyyy"
          :width="150"
        ></DxColumn>
        <DxColumn data-field="visual_test_result" caption="Result">
          <DxLookup :data-source="result" display-expr="desc" value-expr="desc" />
          <DxRequiredRule />
        </DxColumn>

        <DxColumn
          data-field="file_path"
          caption="File"
          :width="150"
          cell-template="file-icon"
          edit-cell-template="file-editor"
        />
        <template #file-editor>
          <div>
            <!-- <i class="fa-regular fa-file" style="display:flex; font-size:36px; padding:10px"></i> -->
            <DxFileUploader
              select-button-text="Upload file"
              label-text="or Drop file here"
              accept
              upload-mode="useForm"
              style="padding-top:10px"
              @value-changed="ON_UPLOAD_CHANGE"
            />
          </div>
        </template>
        <template #file-icon="{data}">
          <div class="d-md-none" style="display:flex" v-if="data.data.file_path">
            <i
              class="fa-regular fa-file-pdf"
              style="display:flex; font-size:18px; justify-content:center"
            ></i>
            <label style="padding-left:5px">{{ data.data.file_name }}</label>
          </div>
        </template>
        <template #dxTextArea="{ data }">
          <div>
            <DxTextArea :read-only="true" :value="data.value" />
          </div>
        </template>
        <!-- Configuration goes here -->
        <!-- <DxFilterRow :visible="true" /> -->
        <DxScrolling mode="standard" />
        <DxSearchPanel :visible="true" />
        <DxPaging :page-size="10" :page-index="0" />
        <DxPager
          :show-page-size-selector="true"
          :allowed-page-sizes="[5, 10, 20]"
          :show-navigation-buttons="true"
          :show-info="true"
          info-text="Page {0} of {1} ({2} items)"
        />
        <DxExport :enabled="true" />
      </DxDataGrid>
    </div>
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import "@fortawesome/fontawesome-free/css/all.css";
//import moment from "moment";

//Components
//import VueTabsChrome from "vue-tabs-chrome";

//FileUpload
import { DxFileUploader } from "devextreme-vue/file-uploader";
import DxTextArea from "devextreme-vue/text-area";
//DataGrid
import "devextreme/dist/css/dx.light.css";
import { DxItem } from "devextreme-vue/form";
import {
  DxDataGrid,
  DxSearchPanel,
  DxPaging,
  DxPager,
  DxScrolling,
  DxColumn,
  DxExport,
  //DxToolbar,
  DxEditing,
  DxLookup,
  DxRequiredRule,
  DxForm
} from "devextreme-vue/data-grid";

//Structures

export default {
  name: "VisualInspection",
  components: {
    DxFileUploader,
    DxDataGrid,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    //DxToolbar,
    DxForm,
    DxItem,
    DxEditing,
    DxLookup,
    DxRequiredRule,
    DxTextArea
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Visual Inspection",
      subpageInnerName: null
    });
    this.FETCH_RECORD();
  },
  data() {
    return {
      dataList: [],
      file: "",
      file_render: "",
      dataEvent: "",
      result: [
        {
          desc: "Accept"
        },
        {
          desc: "Reject"
        }
      ],
      dataGridAttributes: {
        class: "data-grid-style"
      }
    };
  },
  computed: {},
  methods: {
    FETCH_RECORD() {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      axios({
        method: "get",
        url: "/LiftingTest/get-lifting-visual-test-by-id-tag?id_tag=" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          console.log("visual record:");
          if (res.status == 200 && res.data) {
            this.dataList = res.data;
            console.log(this.dataList);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    CREATE_RECORD(e) {
      var formData = new FormData();
      const dateTime = new Date(e.data.visual_test_date);
      const user = JSON.parse(localStorage.getItem("user"));
      formData.append("id_tag", this.$route.params.id_tag);
      formData.append("cert_no", e.data.cert_no);
      formData.append("visual_by", e.data.visual_by);
      formData.append("visual_test_date", dateTime.toISOString());
      formData.append("visual_test_result", e.data.visual_test_result);
      formData.append("created_by", user.id);
      formData.append("file", this.file);
      axios({
        method: "post",
        url: "/LiftingTest/add-lifting-visual-test",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: formData
      })
        .then(res => {
          if (res.status == 201) {
            console.log("visual record created!");
            this.FETCH_RECORD();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    UPDATE_RECORD(e) {
      console.warn(this.file);
      var formData = new FormData();
      const dateTime = new Date(e.data.visual_test_date);
      const user = JSON.parse(localStorage.getItem("user"));
      formData.append("id", e.key);
      formData.append("id_tag", this.$route.params.id_tag);
      formData.append("cert_no", e.data.cert_no);
      formData.append("visual_by", e.data.visual_by);
      formData.append("visual_test_date", dateTime.toISOString());
      formData.append("visual_test_result", e.data.visual_test_result);
      formData.append("updated_by", user.id);
      formData.append("file_path", e.data.file_path);
      formData.append("file_name", e.data.file_name);
      formData.append("file", this.file ?? "");
      axios({
        method: "put",
        url: "/LiftingTest/edit-lifting-visual-test?id=" + e.key,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: formData
      })
        .then(res => {
          if (res.status == 204) {
            console.log("update success");
            this.FETCH_RECORD();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    DELETE_RECORD(e) {
      axios({
        method: "delete",
        url: "/LiftingTest/visual/" + e.key,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          if (res.status == 204) {
            console.log("delete success");
            this.FETCH_RECORD();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    ON_UPLOAD_CHANGE(e) {
      let reader = new FileReader();
      reader.readAsDataURL(e.value[0]);
      reader.onload = () => {
        //for display img when upload
        //Not needed in this case
        this.file_render = reader.result;
      };
      this.file = e.value[0];
    },
    EDIT_START(e) {
      this.file = [];
      this.dataEvent = e;
    },
    SAVE(e) {
      console.log(e);
      console.log(e.changes.length);
      if (e.changes.length == 0) {
        this.UPDATE_RECORD(this.dataEvent);
      }
    }
  }
};
</script>

